import { Ripple } from 'primereact/ripple';

import { MenuItem } from '../../../pages/mindmap/types';

type PanelMenuItemProps = MenuItem & { children?: MenuItem[] };

export const PanelMenuItem = ({ expanded, disabled, icon, label, onClick }: PanelMenuItemProps) => {
  return (
    <li className={`p-ripple p-menuitem ${expanded ? 'p-menuitem-active' : ' '}`} onClick={onClick}>
      <a className={`p-menuitem-link ${disabled ? 'p-disabled' : ''}`} role="menuitem" aria-haspopup="true">
        <span className={`p-menuitem-icon ${icon}`}></span>
        <span className={'p-menuitem-text'}>{label}</span>
        <span className="p-submenu-icon"></span>
        {/* 
        // @ts-ignore */}
        <Ripple ripple={true} />
      </a>
    </li>
  );
};
