import './tool-panel.css';

import React, { useEffect, useRef } from 'react';
import { animated } from 'react-spring';

import { useMindmap } from '../../../pages/mindmap/use-mindmap';
import { PanelMenu, PanelMenuValue } from '../../ui/panel-menu';

export const ToolPanel = () => {
  const {
    copyCutPaste: {
      actions: {
        copySelectable,
        cutSelectable,
        onCopyClick,
        onCutClick,
        onPasteClick,
        onUncutClick,
        pasteSelectable,
        uncutSelectable,
      },
    },
    manipulation: {
      actions: {
        onDeleteClick,
        onAddChildClick,
        onAddTopSiblingClick,
        onAddBottomSiblingClick,
        addBottomSiblingSelectable,
        addTopSiblingSelectable,
        addChildSelectable,
        deleteSelectable,
      },
    },
    interactableMap: { toolPanelOpen, updateStyle, interactableMapSpring, toolPanelRef },
  } = useMindmap();

  // animate mounting appearance
  useEffect(() => {
    if (toolPanelOpen) {
      const toolPanelEl = toolPanelRef.current;
      if (toolPanelEl) {
        toolPanelEl.classList.add('fade-in');
        setTimeout(() => {
          toolPanelEl.classList.remove('fade-in');
        }, 150);
      }
    }
  }, [toolPanelOpen]);

  const panelMenu: PanelMenuValue = [
    {
      label: 'Styling',
      icon: 'pi pi-fw ri-palette-line',
      children: [
        {
          label: 'WIP',
          icon: 'pi ri-brush-3-fill',
          disabled: true,
        },
      ],
    },
    {
      separator: true,
    },
    {
      label: 'Knoten',
      icon: 'pi ri-mind-map',
      children: [
        {
          label: 'Nachbarsknoten',
          icon: 'pi pi-angle-up',
          onClick: onAddTopSiblingClick,
          disabled: addTopSiblingSelectable,
        },
        {
          label: 'Nachbarsknoten',
          icon: 'pi pi-angle-down',
          onClick: onAddBottomSiblingClick,
          disabled: addBottomSiblingSelectable,
        },
        {
          label: 'Unterknoten',
          icon: 'pi pi-angle-right',
          onClick: onAddChildClick,
          disabled: addChildSelectable,
        },
        {
          label: 'Knoten löschen',
          icon: 'pi pi-trash',
          onClick: onDeleteClick,
          disabled: deleteSelectable,
        },
      ],
    },
    {
      separator: true,
    },
    {
      label: 'Copy & Paste',
      icon: 'pi pi-fw pi-pencil',
      children: [
        {
          label: 'Copy',
          onClick: onCopyClick,
          icon: 'pi ri-clipboard-line',
          disabled: !copySelectable,
        },
        {
          label: 'Cut',
          onClick: onCutClick,
          icon: 'pi ri-scissors-cut-line',
          disabled: !cutSelectable,
        },
        {
          label: 'Paste',
          onClick: onPasteClick,
          icon: 'pi ri-file-copy-line',
          disabled: !pasteSelectable,
        },
        {
          label: 'Uncut',
          onClick: onUncutClick,
          icon: 'pi ri-arrow-go-back-line',
          disabled: !uncutSelectable,
        },
      ],
    },
  ];

  const updatePositioning = (ms: number) => {
    // update map style (positioning of tool panel) when tool panel changes dimensions and tool pane is on bottom of screen
    const bottom = document.getElementById('tool-panel-card')?.getBoundingClientRect().bottom;
    const toolpanelBottomIsOnBottomViewport = bottom && bottom > window.innerHeight - 2;
    if (toolpanelBottomIsOnBottomViewport) {
      let counter = 0;
      let interval = setInterval(() => {
        counter += 1;
        updateStyle();
        if (counter > ms / 10) clearInterval(interval);
      }, 10);
    }
  };

  const onExitAnimationStart = () => updatePositioning(450);

  const onEnterAnimationStart = () => updatePositioning(1000);

  const [style] = interactableMapSpring || [];

  return (
    <animated.div
      id="tool-panel-card"
      className="p-contextmenu p-component"
      ref={toolPanelRef}
      style={{
        position: 'absolute',
        left: style?.toolPanelPageX,
        top: style?.toolPanelPageY,
        visibility: toolPanelOpen ? undefined : 'hidden',
      }}
    >
      <PanelMenu
        value={panelMenu}
        onExitAnimationStart={onExitAnimationStart}
        onEnterAnimationStart={onEnterAnimationStart}
      />
    </animated.div>
  );
};
