import { uuidv4 } from '@firebase/util';

import { flatToTree } from '../../utils/flatToTree';
import { FlatNode, OptionalFlatNode, Tree } from './types';
import { Background } from './use-mindmap-background';
import { Data } from './use-mindmap-data';
import { Selection } from './use-mindmap-selection';
import { Viewport } from './use-mindmap-viewport';

export type ManipulationActions = {
  onDeleteClick: () => void;
  onAddChildClick: () => void;
  onAddTopSiblingClick: () => void;
  onAddBottomSiblingClick: () => void;
  deleteSelectable: boolean;
  addChildSelectable: boolean;
  addTopSiblingSelectable: boolean;
  addBottomSiblingSelectable: boolean;
};

export interface Manipulation {
  addChild: (...ids: string[]) => FlatNode[];
  addSibling: (left: boolean, ...ids: string[]) => FlatNode[];
  removeNode: (...ids: string[]) => void;
  updateNode: (nodeUpdates: OptionalFlatNode) => void;
  actions: ManipulationActions;
}

type UseManipulation = { data: Data; background: Background; viewport: Viewport; selection: Selection };

export const useMindmapManipulation = ({
  data: { nodeMap, setNodeMap, rootNodeId },
  background: { undrawLine },
  viewport: { moveIntoViewport },
  selection: { selectedNodeIds },
}: UseManipulation): Manipulation => {
  const addChild = (...ids: string[]): FlatNode[] => {
    let newNodeMap = { ...nodeMap };
    let createdChildren: FlatNode[] = [];

    ids.forEach((id) => {
      let newUuid = uuidv4();
      const newChild: FlatNode = {
        id: newUuid,
        pid: id,
        width: 150 + Math.random() * 200,
        height: 150 + Math.random() * 200,
        type: 'text',
      };
      newNodeMap[newUuid] = newChild;
      createdChildren.push(newChild);
    });

    setNodeMap(newNodeMap);
    moveIntoViewport(createdChildren.map(({ id }) => id));
    return createdChildren;
  };

  const addSibling = (left: boolean, ...ids: string[]): FlatNode[] => {
    let newNodeMap = { ...nodeMap };
    let createdSiblings: FlatNode[] = [];

    // filter out map root node id
    ids = ids.filter((id) => id != rootNodeId);

    ids.forEach((id) => {
      let newUuid = uuidv4();
      const newChild: FlatNode = {
        id: newUuid,
        pid: nodeMap[id].pid,
        width: 150 + Math.random() * 200,
        height: 150 + Math.random() * 200,
        type: 'text',
      };
      newNodeMap = left
        ? {
            [newUuid]: newChild,
            ...nodeMap,
          }
        : {
            ...nodeMap,
            [newUuid]: newChild,
          };
      createdSiblings.push(newChild);
    });

    setNodeMap(newNodeMap);
    moveIntoViewport(createdSiblings.map(({ id }) => id));
    return createdSiblings;
  };

  const removeNode = (...ids: string[]) => {
    if (nodeMap) {
      let newNodeMap = { ...nodeMap };
      let linesToDelete: string[] = [];

      // filter out map root node id
      ids = ids.filter((id) => id != rootNodeId);

      ids.forEach((id) => {
        // get node's subtree
        let subtree = flatToTree(newNodeMap, ({ id: _id }) => _id === id);

        // delete node itself
        linesToDelete.push(id);
        delete newNodeMap[id];

        // delete all child nodes
        if (subtree) {
          let children = subtree.children;
          if (children) {
            let child: Tree | undefined;
            while ((child = children.shift())) {
              delete newNodeMap[child.id];
              linesToDelete.push(child.id);

              if (child.children) {
                children.push(...child.children);
              }
            }
          }
        }
      });

      // update flat node map
      setNodeMap(newNodeMap);

      // remove lines
      undrawLine(...linesToDelete);
    }
  };

  const updateNode = (nodeUpdates: OptionalFlatNode) => {
    const { id } = nodeUpdates;

    if (nodeMap) setNodeMap({ ...nodeMap, [id]: { ...nodeMap[id], ...nodeUpdates } });
  };

  const disabled = selectedNodeIds.length == 0;
  const rootNodeSelected = rootNodeId == selectedNodeIds[0];
  const actions: ManipulationActions = {
    onDeleteClick: () => removeNode(...selectedNodeIds),
    onAddChildClick: () => addChild(...selectedNodeIds),
    onAddTopSiblingClick: () => addSibling(false, ...selectedNodeIds),
    onAddBottomSiblingClick: () => addSibling(false, ...selectedNodeIds),
    addBottomSiblingSelectable: disabled || rootNodeSelected,
    addTopSiblingSelectable: disabled || rootNodeSelected,
    addChildSelectable: disabled,
    deleteSelectable: disabled || rootNodeSelected,
  };

  return { addChild, addSibling, removeNode, updateNode, actions };
};
