import React, { MouseEvent } from 'react';

import { PositionedTree } from '../../../pages/mindmap/types';
import { useMindmap } from '../../../pages/mindmap/use-mindmap';

interface NodeDebugProps {
  showDropPreview: boolean;
  node: PositionedTree;
}

export const NodeDebug = ({ showDropPreview, node }: NodeDebugProps) => {
  const {
    selection: { isNodeSelected },
    dragAndDrop: { dropPreviewNode },
    manipulation: { addChild, addSibling, removeNode, updateNode },
  } = useMindmap();

  const selected = isNodeSelected(node.id);

  const onAddChildClick = (e: MouseEvent) => {
    e.stopPropagation();
    addChild(node.id);
  };

  const onAddRightSiblingClick = (e: MouseEvent) => {
    e.stopPropagation();
    addSibling(false, node.id);
  };

  const onAddLeftSiblingClick = (e: MouseEvent) => {
    e.stopPropagation();
    addSibling(true, node.id);
  };

  const onRemoveNodeClick = (e: MouseEvent) => {
    e.stopPropagation();
    removeNode(node.id);
  };

  const onUpdateNodeClick = (e: MouseEvent) => {
    e.stopPropagation();
    updateNode({ id: node.id, width: 150 + Math.random() * 200, height: 150 + Math.random() * 200 });
  };

  return (
    <div style={{ border: '1px solid black', height: node.height, width: node.width }}>
      <p>
        {`id[${node.id.substring(0, 4)}], x[${node.x.toFixed(0)}], y[${node.y.toFixed(0)}], w[${node.width.toFixed(
          0,
        )}], h[${node.height.toFixed(0)}], BRx[${(node.x + node.width).toFixed(0)}], BRy[${(
          node.y + node.height
        ).toFixed(0)}]`}
        <br />
        Selected: {selected ? 'Yes' : 'No'}
        <br />
        Ghost-Node: {showDropPreview ? dropPreviewNode?.position : 'No'}
      </p>
      <button onClick={onAddChildClick}>Add child</button> <br />
      <button onClick={onAddLeftSiblingClick}>Add left sibling</button>
      <button onClick={onAddRightSiblingClick}>Add right sibling</button>
      <br />
      <button onClick={onRemoveNodeClick}>Remove node</button>
      <br />
      <button onClick={onUpdateNodeClick}>Update node</button>
    </div>
  );
};
